
  export default{
    props: {
      postId: {
        type: Number,
        required: true,
      }
    },
    data(){
      return{ 
        valid: false,
        content: '',
        success: false,
        successMessages: '',
        hideDetails: true,
      }
    },
    computed:{
      me(){
        return this.$store.state.user.me;
      }
    },
    methods:{
      onChangeTextarea(value){
        if(value.length){
          this.hideDetails = true;
          this.success = false;
          this.successMessages = '';
        }
      },
      async onSubmitForm(){
        if(this.$refs.form.validate()){
          try{  
            if(!this.content.length) return;
            
            const addCommentRes = await this.$store.dispatch('post/addComment', {
              postId: this.postId,
              content: this.content
            });

            if(!addCommentRes.result){
              alert(addCommentRes.msg);
              return;
            }
            this.content = '';
            this.success = true;
            this.successMessages = '댓글이 작성되었습니다.';
            this.hideDetails = false;
          }catch(err){
            alert(err);
          }
        }
      }
    }
  }
