
  import CommentForm from '~/components/CommentForm.vue';
  import postImages from '~/components/PostImages.vue';
  
  export default{
    components: { postImages, CommentForm },
      props:{
        post:{
         type: Object,
         required: true,
        }
      },
      data() {
        return {
          commentOpened: false
        }
      },
      methods:{
        async onRemovePost(){
          try{
            const removePostRes = await this.$store.dispatch('post/removePost', {
              postId: this.post.id
            });

            if(!removePostRes.result){
              alert(removePostRes.msg);
              return;
            }

            alert('삭제되었습니다.');

          }catch(err){
            console.log(err);
          }
        },
        async onLike(){
          try{
            const onLikeRes = await this.$store.dispatch('post/onLike', {
              postId: this.post.id,
              is_like: this.post.is_like == '1'? '0' : '1'
            });

            if(!onLikeRes.result){
              alert(onLikeRes.msg);
              return;
            }

          }catch(err){
            console.log(err);
          }
        },
        onEditPost(){

        },
        onToggleComment(){
          this.commentOpened = !this.commentOpened;
        }
    }
  }
